import React from 'react'

class MessageBox extends React.Component {
    
    render() {
        return (
            <div>
                <b>Messages</b><br/><br/>
                <ul >
                    {this.props.message.map((item,index) => {
                        return (
                            <li key={index}>{item}</li> 
                        )
                    })}
                </ul>
            </div>
        )
    }
}

export default MessageBox
